:root {
    --background-color: #9b7676;
    --text-color: white;
}

body {
    margin: 0;
    color: var(--text-color);
    font-family: "spectral", serif;
    font-weight: 300;
    font-size: 0.5rem;
    position: relative;
    /* background-color: #d2f7de; */
}

header {
    /* background:url('minted.svg') left center no-repeat cover; */
}

.pull-tab {
    /* background: url('pull-tab.svg') center bottom no-repeat; */
    /* background-size: cover; */
    /* padding: 2.2rem; */
    margin-top: 33vh;
    display: block;
    width: 100%;
}

.background {
    background:
        url("line-art-02.svg"),
        var(--background-color);
    background-size: 121vw;
    background-position:
        center 150vh,
        center top;
    background-repeat: no-repeat;
    height: 8500px;
    padding-top: 100vh;
    position: relative;
}

.background::after {
    content: '';
    position: absolute;
    top: 150vh;
    width: 100%;
    height: 200vh;
    background: linear-gradient(var(--background-color), transparent);
}

.sr-only {
    border: 0 !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
    /* 1 */
    -webkit-clip-path: inset(50%) !important;
    clip-path: inset(50%) !important;
    /* 2 */
    height: 1px !important;
    margin: -1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important;
    white-space: nowrap !important;
    /* 3 */
}

.flow-text {
    padding: 2.5rem;
    position: absolute;
    /* border: 2px solid var(--text-color); */
    /* clip-path: polygon(0 30%, 36% 0, 86% 0, 100% 54%, 75% 100%, 0 82%); */
    /* clip-path: url('#svgPath'); */
    /* background-color: var(--background-color); */
    top: 50%;
    position: fixed;
    width: 60%;
    display: inline-block;
    left: 53%;
    transform: translate(-50%, -50%);
    padding: 6.5rem 2.5rem 5.5rem;
    line-height: 1.3;
    background: url('round-blob.svg') center center no-repeat;
    background-size: cover;
}

h2 {
    font-weight: 600;
    font-size: 1.3rem;
}

.yatra {
    font-family: 'yatra one';
}

.heading-text {
    color: var(--background-color);
    z-index: 1;
    font-size: 0.8rem;
    font-weight: 200;
    margin-top: 1rem;
    margin-left: 1.5rem;
}

strong {
    margin-left: 0.5em;
    font-weight: 600;
}